<template>
    <div class = "friend">
        <div class = "title">Friends</div>
        <div class = "allfriends" v-for="friend in friends" :key = "friend.id">
            <div class = "onefriend">
                <a :href = "`/chatpage?friendid=` + friend.id" class = "name">{{friend.name}}</a>
                <div  @click= "delFriend(friend.name)" class = "del hvr-shutter-out-horizontal"> 删除 </div>
            </div>
        </div>
        <div class = "addfriend">
            <div class = "login_input">
                <input type="text" v-model="findName" placeholder="输入好友的用户名">
            </div>
            <div class = "submit">
                <button @click = "addfriend(0)" class="submit_input hvr-shutter-out-horizontal">添加好友</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() 
    {
        return {
            friends: [],
            findName : ""
        }
    },
    methods:
    {
        queryfriend(value)
        {
            var xhr = new XMLHttpRequest();
            xhr.open("GET","http://127.0.0.1:8080/finduser?username=" + this.findName);
            xhr.send();
            xhr.onreadystatechange = () => {   
                if (xhr.readyState === 4 && xhr.status === 200)
                {
                    // console.log(xhr.responseText);
                    const resp = JSON.parse(xhr.responseText);
                    console.log("resp = ",resp);
                    if(resp !== null)
                    {
                        if(value)
                        {
                            for(let friend of resp)
                            {
                                this.friends.push({"id" : friend.Uid, "name" : friend.Username});
                            }
                        }
                        else
                        {
                            this.friends.push({"id" : resp.ID, "name" : resp.Username})
                        }

                    }
                }
            };
            return {};
        },
        addfriend(value)
        {
            console.log(this.findName);
            if(this.findName != "" || value)
            {
                this.queryfriend(value);
            }
        },
        delFriend(name)
        {
            var xhr = new XMLHttpRequest();
            xhr.open("GET","http://127.0.0.1:8080/delfriend?friendname=" + name);
            xhr.send();
            xhr.onreadystatechange = () => {  
                if (xhr.readyState === 4 && xhr.status === 200)
                {
                    for(let idx in this.friends)
                    {
                        if(this.friends[idx].name === name)
                        {
                            this.friends.splice(idx, 1);
                            break;
                        }
                    }
                }
            }
        }

    },
    mounted()
    {
        this.friends = [];
        this.friend = "";
        this.addfriend(1);
    }
}
</script>

<style scoped>

.friend {
    margin: auto auto auto auto;
    width: 500px;
    height: auto;
    border: 1px solid #bfbfbf;
    transition: all 0.5s ease 0s;
}

.friend:hover {
    box-shadow: 0 0 3px 0px;
}

.title {
    font-size: 20px;
    padding-bottom: 10px;
    padding-top: 10px;
    text-align: center;
    color: #212121;
    border-bottom: 1px solid #bfbfbf;
    background-color: #ffe3f0;
}

.username:hover {
    color: #ff409f;
}

.allfriends {
    width: auto;
    height: auto;
    background-color: #fffafc;
}
.onefriend {
    position: relative;
    display: block;
    height: 30px;
    margin: 0px 20px;
    padding: 20px 10px;
    /* background-color: #ffa1d0; */
}

.name {
    position: absolute;
    background-color: #ffa1d0;
    padding: 5px;
    border-radius: 5px;
}

.addfriend {
    width: auto;
    height: 123px;
    border-style: none;
    resize: none;
    outline: none;
    padding: 5px;
    background-color: #fff2f9;
}

.login_input {
    margin: 14px auto;
    width: 400px;
    height: 40px;
}


.login_input input {
    width: 400px;
    height: 40px;
    outline: none;
    border: 1px solid #bfbfbf;
    padding-left: 7px;
    border-radius: 5px;
}

.login_input input:focus {
    border-color: #ff7fbf;
}


.submit {
    margin: auto;
    width: 400px;
    height: 40px;
    /* padding-top: 24px; */
}

.submit_input {
    margin: 0px 140px;
    width: 130px;
    height: 45px;
    /* background-color: rgb(255, 0, 0); */
    font-weight: 550;
    font-size: 18px;
    border-style: none;
    /* 鼠标变手 */
    cursor: pointer
}

/* Shutter Out Horizontal */
.hvr-shutter-out-horizontal {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    background: #d4d4d4;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.hvr-shutter-out-horizontal:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #ff7fbf;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 50%;
    transform-origin: 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.hvr-shutter-out-horizontal:hover,
.hvr-shutter-out-horizontal:focus,
.hvr-shutter-out-horizontal:active {
    color: white;
}

.hvr-shutter-out-horizontal:hover:before,
.hvr-shutter-out-horizontal:focus:before,
.hvr-shutter-out-horizontal:active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}


.del {
    position: absolute;
    /* height: auto; */
    width:50px;
    padding: 5px;
    right: 0%;
    text-align: center;
    background-color: #ffe3f0;
    display: block;
    border-radius: 5px;
}
</style>